export default function Uploader(props) {
    const classes = useStyles();
    const history = useHistory();
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [tempFile, setTempFile] = useState(null);
    const [loadingDialogOpen, setLoadingDialogOpen] = useState(false);
    const [fileDialogOpen, setFileDialogOpen] = useState(false);
    const [uploadState, setUploadState] = useState({
        upload: false,
        ocr: false
    });
    const [fileList, setFileList] = useState(null);
    const [docList, setDocList] = useState(null);

    const handleFile = (e) => {
        e.preventDefault();
        setTempFile(e.target.files[0]);
    }

    const submitFile = (e) => {
        e.preventDefault();
        if (tempFile !== null) {
            props.getFile(tempFile);
            setLoadingDialogOpen(true);
            axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                "action": "UPLOAD",
                "file": tempFile.name
            })
            .then((res)=> {
                axios.put(res.data.body.upload_url, tempFile)
                .then((res) => {
                    setUploadState({...uploadState, upload: true});
                    axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                        "action": "OCR",
                        "file": tempFile.name
                    }, {headers : {"InvocationType":"Event"}})
                    .then((res) => {
                        const timer = setInterval(() => {
                            axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                                "action": "STATUS",
                                "file": tempFile.name
                            })
                            .then((res) => {
                                if (res.data.body.result === "OK") {
                                    clearInterval(timer);
                                    setUploadState({...uploadState, ocr: true});
                                    setLoadingDialogOpen(false);
                                    enqueueSnackbar("업로드 완료", {variant: 'success'});
                                    loadFileList();
                                }
                            })
                        }, 5000);
                        // 2 min 초과 시 타임아웃
                    })
                })
            })
            .catch((err)=> {
                console.log(err);
                setLoadingDialogOpen(false);
                enqueueSnackbar('에러 발생', {variant: 'error'});
            })
        } else {
            enqueueSnackbar('파일이 선택되지 않았습니다', {variant: 'error'});
        }
    }

    const loadFileList = () => {
        axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                "action": "FLIST"
            })
            .then((res)=> {
                console.log(res.data.body.data)
                setFileList(res.data.body.data)
            })
            .catch((err)=> {
                console.log(err);
                enqueueSnackbar('에러 발생', {variant: 'error'});
            });
    }

    const loadDocList = (fileId) => {
        enqueueSnackbar('Loading...');
        axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                "action": "WPLIST",
                "file_id": fileId
            })
            .then((res)=> {
                console.log(res.data.body.data);
                setDocList(res.data.body.data);
                closeSnackbar();
                setFileDialogOpen(true);
            })
            .catch((err)=> {
                console.log(err);
                enqueueSnackbar('에러 발생', {variant: 'error'});
            })
    }

    const InspectBtn = (props) => {
        return (
        <Button className={props.disabled ? classes.disabledBtn : classes.inspectBtn} disabled={props.disabled}>
            검수
        </Button>)
    }

    const EditBtn = (props) => {
        return (
        <Button className={props.disabled ? classes.disabledBtn : classes.editBtn} disabled={props.disabled}>
            편집
        </Button>)
    }

    const showBtn = (status) => {
        switch (status) {
            case "READY":
                return (<EditBtn />)
            case "INPSECT":
            case "COMPLETE":
                return (<InspectBtn />)
            default:
                return (<Button className={classes.disabledBtn}></Button>)
        }
    }

    useEffect(() => {
        loadFileList();

    }, [])

    return (
        <>
        <div className={classes.uploader}>
            <Box fontWeight="fontWeightBold" fontSize={24} m={1}>
                Upload Data
            </Box>
            <div className={classes.controller}>
                <Button variant="outlined" component="label" className={classes.uploadBtn}>
                    <input type="file" style={{display:"none"}} accept="application/pdf" onChange={handleFile} />
                    {tempFile==null ? "업로드" : "업로드 : "+ tempFile.name}
                </Button>
                {tempFile !== null ? <Button variant="outlined" color="primary" onClick={submitFile}>
                    <CachedIcon fontSize='small'/>&nbsp;&nbsp;업로드 실행
                </Button> : null}
            </div>
        </div>

        <div className={classes.fileManager}>
            <Box fontWeight="fontWeightBold" fontSize={24} m={1}>
                업로드 파일 관리
            </Box>
            <div className={classes.headerRow}>
                    <div>일련번호</div><div>파일 이름</div><div>업로드 시각</div><div>검수 완료 시각</div><div>상태</div>
            </div>
            <div className={classes.fileList}>
                {fileList ? fileList.map((file, idx) => {
                    return (<>
                    <div className={classes.fileRow} onClick={() => loadDocList(file.id)}>
                        <div>{file.id}</div><div>{file.file_name}</div><div>{file.upload_time}</div><div>{file.complete_time}</div><div>{file.status}</div>
                    </div>
                    <div className={classes.rowDivider}/>
                    </>)
                }) : null}
            </div>
        </div>
        

        <Dialog open={loadingDialogOpen} aria-labelledby="upload-dialog" maxWidth='xs' fullWidth={true}>
            <DialogTitle id="upload-dialog">업로드 및 OCR</DialogTitle>
            <List className={classes.dialogContent}>
                <ListItem>
                    <Typography variant="h6" noWrap >1) 업로드 진행 &nbsp;&nbsp;</Typography>
                    {!uploadState.upload ? <CircularProgress size={20}/> : <DoneIcon />}
                </ListItem>
                <ListItem>
                    <Typography variant="h6" noWrap >2) OCR 진행 &nbsp;&nbsp;&nbsp;</Typography>
                    {!uploadState.ocr ? <CircularProgress size={20}/> : <DoneIcon />}
                </ListItem>
            </List>
        </Dialog>

        <Dialog open={fileDialogOpen} className={classes.fileDialog} aria-labelledby="file-dialog" maxWidth={false} fullWidth={true}>
            <div className={classes.fileDescription}>
                <Box fontWeight="fontWeightBold" fontSize={24} m={1} style={{display: 'flex'}}>
                    파일 상세 
                    <Button className={classes.closeDialogBtn} onClick={() => setFileDialogOpen(false)}>닫기</Button>
                </Box>
                
                <div className={classes.dialogHeaderRow}>
                        <div>ID</div><div>TYPE</div><div>CREATE_TIME</div><div>LAST_UPDATE_TIME</div><div>STATUS</div>
                </div>
                <div className={classes.docList}>
                    {docList ? docList.WPS_List ? docList.WPS_List.map((doc, idx) => {
                        return (<>
                        <div className={classes.docRow}>
                            <div>{doc.id}</div><div>{doc.code_type}</div><div>{doc.upload_time}</div><div>{doc.inspect_time}</div><div>{doc.status}</div><div>{showBtn(doc.status)}</div>
                        </div>
                        <div className={classes.rowDivider}/>
                        </>)
                    }) : null : null}
                    {docList ? docList.PQR_List ? docList.PQR_List.map((doc, idx) => {
                        return (<>
                        <div className={classes.docRow}>
                            <div>{doc.id}</div><div>{doc.code_type}</div><div>{doc.upload_time}</div><div>{doc.inspect_time}</div><div>{doc.status}</div><div>{showBtn(doc.status)}</div>
                        </div>
                        <div className={classes.rowDivider}/>
                        </>)
                    }) : null : null}
                </div>
            </div>
        </Dialog>

        <div className={classes.version}>220105-1806</div>
        </>
    );
}