import React from "react"
import { Route, Redirect } from "react-router-dom"
import crypto from 'crypto';


function AuthRoute({component: Component, render, ...rest}) {
    return (
        <Route {...rest} 
            render = {(props) => sessionStorage.accessToken && crypto.createHash('sha256').update(sessionStorage.accessToken).digest('hex') === "d042be4d7ca43ee945a7aa49ad867d705e611d39d94704b65faf769fd1b26a8d" ? (render ? render(props) : (<Component {...props} />)) 
                    : (<Redirect to={{pathname: "/login", state: {from: props.location}}} 
                    />)
            }
            />)
}

export default AuthRoute;